import contact from "@/components/contact/contact.vue"
import counter from "@/components/counter/counter.vue"
import link2 from "@/components/link2/link2.vue"
import pinpai from "@/components/pinpai/pinpai.vue"
import banner from "@/components/banner/banner.vue"
export default {
	components:{contact,counter,pinpai,banner,link2},
	data() {
		return {
			title: 'Hello',
			production:null,
			times:0
		}
	},
	onLoad() {
		
		this.getPar();
	},
	methods: {
		toUrl(url){
			uni.navigateTo({
				"url":url
			})
		},
		getPar(){
			uni.showLoading({
				"title":"正在获取免费次数请稍后",
				"mask":"true"
			})
			this.$ajax("/my/freeCount","POST",{moduleCode:'nav'}).then((res)=>{
				console.log(res);
				if(res != 0){
					uni.hideLoading();
					uni.showToast({
						"title":"您的生成次数还剩"+res+"次",
						"icon":"none"
					})
					this.times = res
				}else{
					
						uni.showToast({
							"title":"您的免费次数不足正在跳转次数购买页",
							"mask":"true",
							"icon":"none"

						})
						uni.reLaunch({
							"url":"/pages/address/msdh/index"
						})
						
					
					
					
				}
			})
		},
		getLocation(){
			
		}
		
	}
}
